"use client";
import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";

const About = () => {
  const team = [
    { 
      name: "นายธนกฤต สุปัญญา", 
      role: "CEO & Founder", 
      img: "/image/Profile.jpg" // ใส่รูปจริงของคุณใน public/image/
    },
  ];

  return (
    <div className="container py-5">
      {/* Section: About Company */}
      <div className="text-center mb-5">
        <h1 className="mb-3">เกี่ยวกับเรา</h1>
        <p className="lead">
          บริษัท <b>Sakura Travel Japan</b> เป็นบริษัทท่องเที่ยวในประเทศญี่ปุ่น
          ที่มุ่งเน้นการบริการคุณภาพสูง พร้อมสร้างประสบการณ์ท่องเที่ยวที่น่าจดจำ
          ทั้งทัวร์วัฒนธรรม อาหารท้องถิ่น และสถานที่ท่องเที่ยวชื่อดัง
          <br />
          เรามุ่งมั่นให้ลูกค้าได้รับความประทับใจในทุกการเดินทาง
          โดยมีทีมงานมืออาชีพคอยให้คำปรึกษาและดูแลอย่างใกล้ชิด
        </p>
      </div>

      {/* Section: Team */}
      <div className="text-center mb-4">
        <h2>ผู้จัดทำ</h2>
      </div>

      <div className="row justify-content-center">
        {team.map((member, index) => (
          <div className="col-md-3 mb-4" key={index}>
            <div className="card h-100 shadow-sm">
              <img
                src={member.img}
                className="card-img-top"
                alt={member.name}
              />
              <div className="card-body text-center">
                <h5 className="card-title">{member.name}</h5>
                <p className="card-text text-muted">{member.role}</p>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default About;
